<template>

  <div>
    <van-nav-bar title="我的订单" left-arrow left-text="返回" @click-left="$router.back()"></van-nav-bar>
    <div class="list noTabBarList" @scroll="handleScroll" ref="list">
      <div v-for="order in list" :key="order.id" class="item-gap">
        <van-cell :title="`订单ID：${order.id}`" is-link :to="{name:'order', params:{id:order.id}}" />
        <van-cell-group>
          <van-cell v-for="product in order.orderProducts" :key="product.id" :title="product.name"
            :value="`${product.count}件`" :label="`￥${product.price}`" :icon="product.cover" is-link
            :to="{name:'product', params:{id:product.id}}" />
        </van-cell-group>
        <van-cell title=" " :value="`合计：￥${order.total_cost}`" />
      </div>
      <van-loading v-if="isLoading" color="#1989fa" />
    </div>

  </div>
</template>

<script>
  export default {
    data() {
      return {
        isLoading: false,
        orderData: {
          page: 1,
          total: 1,
          list: [],
          scrollTop: 0
        }
      }
    },
    computed: {
      page() {
        return this.orderData.page
      },
      total() {
        return this.orderData.total
      },
      list() {
        return this.orderData.list
      }
    },
    methods: {
      handleScroll() {
        if (this.page == this.total) {
          return
        }
        if (this.isLoading) {
          return
        }
        if (this.$refs.list.clientHeight + this.$refs.list.scrollTop === this.$refs.list.scrollHeight) {
          this.isLoading = true
          this.orderData.page++
          this.getData()
        }
      },
      getData() {
        let params = {
          page: this.page,
          size: 10
        }
        this.$axios.get(`/order/pagination`, {
          params
        }).then(res => {
          if (res.errcode == 0) {
            let data = res.data
            this.orderData.total = data.totalPages
            this.orderData.list = this.orderData.list.concat(data.data)
          }
          this.isLoading = false
        })
      }
    },
    mounted() {
      this.getData()
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .van-loading {
    text-align: center;
    padding-bottom: 20px;
  }

  .van-icon-search {
    font-size: 5vw;
  }
</style>


